.navbar {
  background-color: #333;
  padding: 15px 20px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 2rem;
}

.navbar-title {
  color: white;
  font-size: 24px;
  font-weight: bold;
}

.navbar-menu {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.navbar-item {
  margin: 0 20px;
  display: flex;
  align-items: center;
}

.navbar-link {
  text-decoration: none;
  color: white;
  display: flex;
  align-items: center;
  font-size: 13px;
}

.navbar-icon {
  width: 18px;
  height: 18px;
  margin-right: 4px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.profile-icon {
  background-image: url("../assets/icon/profile.png");
}

.arqueologia-icon {
  background-image: url("../assets/icon/archeology.png");
}

.lienzo-icon {
  background-image: url("../assets/icon/canvas.png");
}

.murales-icon {
  background-image: url("../assets/icon/mural.png");
}

.papel-icon {
  background-image: url("../assets/icon/paper.png");
}

.escultura-icon {
  background-image: url("../assets/icon/sculpture.png");
}

.tejidos-icon {
  background-image: url("../assets/icon/textile.png");
}

.contact-icon {
  background-image: url("../assets/icon/contact.png");
}

.navbar-divider {
  height: 30px;
  width: 1px;
  background-color: white;
  margin: 0 2px;
}


@media (max-width: 550px) {
  .navbar-menu {
    display: none;
  }
  .navbar-container {
    justify-content: center;
  }
}