.contact-section {
  padding: 30px 20px;
  background-color: #f0f0f0;
  text-align: center;
  margin-top: 10px;
}

.contact-title {
  font-family: "Playfair Display", serif;
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 15px;
}

.contact-description {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  color: #555;
  margin-bottom: 20px;
}

.contact-links {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.contact-link {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  text-decoration: none;
  color: #333;
  border: 2px solid #ddd;
  padding: 10px 15px;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.contact-link:hover {
  background-color: #333;
  color: #fff;
}
