.canvas-section {
  padding: 30px;
  background-color: #fff;
  text-align: center;
}

.section-title {
  font-family: "Playfair Display", serif;
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 15px;
}

.canvas-slider {
  max-width: 600px;
  margin: 0 auto;
}

.slider-item {
  position: relative;
  text-align: center;
}

.slider-description {
  margin-top: 10px;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  color: #555;
}
