.profile {
  padding: 70px;
  text-align: center;
  background-color: #fff;
}

.profile-title {
  font-family: "Merriweather", serif;
  font-size: 1.8rem;
  color: #222;
}

.profile-description {
  font-family: "Lato", sans-serif;
  color: #555;
  margin-top: 10px;
}

.profile-image {
  width: 150px;
  border-radius: 50%;
  margin-top: 20px;
}
