/*
@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
*/
.archaeology-section {
  padding: 30px;
  background-color: #f9f9f9;
  text-align: center;
}

.section-title {
  font-family: "Playfair Display", serif;
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 15px;
}

.archaeology-slider {
  max-width: 600px;
  margin: 0 auto;
}

.slider-item {
  position: relative;
  text-align: center;
}

.slider-image {
  max-height: 300px;  /* Máximo alto */
  max-width: 100%;    /* Ancho relativo al contenedor */
  height: auto;       /* Ajusta la altura automáticamente */
  width: auto;        /* Ajusta el ancho automáticamente */
  margin: 0 auto;     /* Centra las imágenes */
  display: block;     /* Elimina el margen inline */
  object-fit: contain; /* Mantén la proporción dentro de su contenedor */
}

.slider-description {
  margin-top: 10px;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  color: #555;
}

.slick-prev,
.slick-next {
  background-color: #333;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.9;
  transition: background-color 0.3s ease, opacity 0.3s ease;
}

.slick-prev:focus,
.slick-next:focus,
.slick-prev:active,
.slick-next:active {
  background-color: #333;
  opacity: 0.9;
}

.slick-prev:hover,
.slick-next:hover {
  background-color: #555;
  opacity: 1;
}

.slick-prev:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-right: 10px solid white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.slick-next:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-bottom: 5px solid transparent;
  border-top: 5px solid transparent;
  border-left: 10px solid white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.slick-prev {
  left: -45px;
}

.slick-next {
  right: -45px;
}

.slick-dots li button:before {
  font-size: 5px;
  color: lightgray;
  opacity: 0.7;
  transition: all 0.3s ease;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 1;
  font-size: 10px;
}
